<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId" :color-filter.sync = "filterColor"/>
      <section class="catalog">
        <div class="preloader" v-if="productsLoading">
          <svg id="loader" width="100" height="100" viewBox="0 0 100 100" fill="none" xmlns="http://www.w3.org/2000/svg">
            <g clip-path="url(#clip0_121_2401)">
              <path d="M14.0002 50.0005C14.0002 69.8825 30.1182 86.0005 50.0002 86.0005C69.8822 86.0005 86.0002 69.8825 86.0002 50.0005C86.0002 30.1185 69.8823 14.0005 50.0003 14.0005C45.3513 14.0005 40.9082 14.8815 36.8282 16.4865" stroke="#9873FF" stroke-width="8" stroke-miterlimit="10" stroke-linecap="round"/>
            </g>
            <defs>
              <clipPath id="clip0_121_2401">
                <rect width="100" height="100" fill="white"/>
              </clipPath>
            </defs>
          </svg>
        </div>
        <div v-if="productsLoadingFailed">Произошла ошибка при загрузке товаров
          <button @click="loadProducts">Попробовать еще раз</button>
        </div>
        <ProductList :products = 'products'/>
        <BasePagination v-model = 'page' :count = 'countProducts' :per-page = 'productsPerPage'/>
      </section>  
    </div>
  </main>
</template>

<script>
import ProductList from '@/components/ProductList'
import BasePagination from '@/components/BasePagination.vue'
import ProductFilter from '@/components/ProductFilter.vue'
import axios from 'axios'
import {API_BASE_URL} from '@/config'

export default {
  components: {ProductList, BasePagination, ProductFilter},
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColor: 0,
      page: 1,
      productsPerPage: 3,
      productsData: null,
      productsLoading: false,
      productsLoadingFailed: false
    }
  },
  computed: {
    products() {
      return this.productsData ?
      this.productsData.items.map(product => {
        return {
          ...product,
          image: product.image.file.url
        }
      }) 
      : []
    },

    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0
    }
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false
      clearTimeout(this.loadProductsTimer)
      this.loadProductsTimer = setTimeout(() => {
        axios
        .get(API_BASE_URL + `/api/products`, {
          params: {
            page: this.page,
            limit: this.productsPerPage,
            categoryId: this.filterCategoryId,
            minPrice: this.filterPriceFrom,
            maxPrice: this.filterPriceTo,
          }
        })
          .then(response => this.productsData = response.data)
          .catch(() => this.productsLoadingFailed = true)
          .then(() => this.productsLoading = false);
      }, 2000)
    }
   
  },
  watch: {
    page() {
      this.loadProducts()
    },
    filterCategoryId() {
      this.loadProducts()
    },
    filterPriceFrom() {
      this.loadProducts()
    },
    filterPriceTo() {
      this.loadProducts()
    },
  },
  created() {
      this.loadProducts()
  }
}
</script>
<style>@import '@/assets/preloader.css';</style>