<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ $store.getters.cartDetailProducts.length }} товара
      </span>
    </div>

    <section class="cart">
      <div v-if="loading">
        <svg id="loader" width="100" height="100" viewBox="0 0 100 100" fill="none" xmlns="http://www.w3.org/2000/svg">
          <g clip-path="url(#clip0_121_2401)">
            <path d="M14.0002 50.0005C14.0002 69.8825 30.1182 86.0005 50.0002 86.0005C69.8822 86.0005 86.0002 69.8825 86.0002 50.0005C86.0002 30.1185 69.8823 14.0005 50.0003 14.0005C45.3513 14.0005 40.9082 14.8815 36.8282 16.4865" stroke="#9873FF" stroke-width="8" stroke-miterlimit="10" stroke-linecap="round"/>
          </g>
          <defs>
            <clipPath id="clip0_121_2401">
              <rect width="100" height="100" fill="white"/>
            </clipPath>
          </defs>
        </svg>
      </div>
      <form v-else class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText v-model="formData.name" title="ФИО" :error="formError.name" placeholder="Введите ваше полное имя" />

            <label class="form__label">
              <input class="form__input" v-model="formData.address" type="text" name="address" placeholder="Введите ваш адрес">
              <span class="form__value">Адрес доставки</span>
              <span v-if="formError.address" class="form__error">{{formError.address}}</span>
            </label>

            <label class="form__label">
              <input class="form__input" v-model="formData.phone" type="tel" name="phone" placeholder="Введите ваш телефон">
              <span class="form__value">Телефон</span>
              <span v-if="formError.phone" class="form__error">{{formError.phone}}</span>
            </label>

            <label class="form__label">
              <input class="form__input" v-model="formData.email" type="email" name="email" placeholder="Введи ваш Email">
              <span class="form__value">Email</span>
              <span v-if="formError.email" class="form__error">{{formError.email}}</span>
            </label>

            <BaseFormTextarea v-model="formData.comment" title="Комментарий к заказу" :error="formError.comment" placeholder="Ваши пожелания"/>
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">    
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in $store.getters.cartDetailProducts" :key="item.productId">
              <h3>{{item.product.title}}</h3>
              <b>{{item.product.price * item.amount | numberFormat}} ₽</b>
              <span>Артикул: {{item.product.id}}</span>
            </li>
          </ul>
          
          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{$store.getters.cartDetailProducts.length}}</b> товара на сумму <b>{{$store.getters.cartTotalPrice | numberFormat}} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{formErrorMessage}}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormField from '@/components/BaseFormField.vue';
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import numberFormat from '@/helpers/numberFormat';
export default {
    components: {BaseFormField, BaseFormText, BaseFormTextarea},
    data() {
        return {
            formData: {},
            formError: {},
            formErrorMessage: '',
            loading: false
        }
    },
    methods: {
        order() {
            this.formError = {}
            this.formErrorMessage = ''
            this.loading = true
            clearTimeout(this.loadProductsTimer)
            this.loadProductsTimer = setTimeout(() => {
            return axios
                .post(API_BASE_URL + `/api/orders`, {
                    ...this.formData
                }, {
                    params: {
                        userAccessKey: this.$store.state.userAccessKey
                    }
                })
                .then(response => {
                    this.$store.commit('resetCart');
                    this.$store.commit('updateOrderInfo', response.data);
                    this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
                })
                .catch((error) => {
                    this.formError = error.response.data.error.request || {};
                    this.formErrorMessage = error.response.data.error.message;
                })
                .then(() => this.loading = false);
            }, 2000)    
        }
    },
    filters: {
      numberFormat
    },
}
</script>
<style>@import '@/assets/preloader.css';</style>