<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{products.length}} товара
      </span>
    </div>

    <section class="cart">
      <div class="preloader" v-if="loading">
        <svg id="loader" width="100" height="100" viewBox="0 0 100 100" fill="none" xmlns="http://www.w3.org/2000/svg">
          <g clip-path="url(#clip0_121_2401)">
            <path d="M14.0002 50.0005C14.0002 69.8825 30.1182 86.0005 50.0002 86.0005C69.8822 86.0005 86.0002 69.8825 86.0002 50.0005C86.0002 30.1185 69.8823 14.0005 50.0003 14.0005C45.3513 14.0005 40.9082 14.8815 36.8282 16.4865" stroke="#9873FF" stroke-width="8" stroke-miterlimit="10" stroke-linecap="round"/>
          </g>
          <defs>
            <clipPath id="clip0_121_2401">
              <rect width="100" height="100" fill="white"/>
            </clipPath>
          </defs>
        </svg>
      </div>
      <div v-else-if="error">
        Произошла ошибка при загрузке корзины
        <button @click.prevent="loadCart">Попробовать еще раз</button>
      </div>
      <div v-else>
        <form class="cart__form form" action="#" method="POST">
          <div class="cart__field">
            <ul class="cart__list">
              <CartItem v-for="item in products" :key="item.productId" :item="item"/>
            </ul>
          </div>

          <div class="cart__block">
            <p class="cart__desc">
              Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
            </p>
            <p class="cart__price">
              Итого: <span>{{totalPrice | numberFormat}} ₽</span>
            </p>

            <router-link :disabled="products.length === 0" tag="button" :to="{name: 'order'}" class="cart__button button button--primery" type="submit">
              Оформить заказ
            </router-link>
          </div>
        </form>
      </div>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat'
import CartItem from '@/components/CartItem.vue'
import { mapGetters } from 'vuex'
export default {
    filters: {
      numberFormat
    },
    data() {
      return {
        loading: false,
        error: false,
      };
    },
    components: {CartItem},
    computed: {
      ...mapGetters({products: 'cartDetailProducts', totalPrice: 'cartTotalPrice'}),
    },
    methods: {
      loadCart() {
        this.loading = true;
        this.error = false;
        clearTimeout(this.loadProductsTimer)
        this.loadProductsTimer = setTimeout(() => {
          this.$store.dispatch("loadCart")
          .then(() => {
            this.loading = false;
          })
          .catch(() => {
            this.loading = false;
            this.error = true;
          });
        },2000)
      },
    },
    created() {
      this.loadCart();
    },
}
</script>
<style>
  @import '@/assets/preloader.css';
</style>